  flex-direction: column;
  overflow: hidden;
}
.tile .header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-grow: 0;
  flex-shrink: 0;
  padding-bottom: inherit;
  line-height: 1em;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.tile .header .title {
  color: hsla(220, 39%, 65%, .8);
  font-size: 1.2em;
  font-weight: 100;
}
.tile .body {
  flex-grow: 1;
  flex-shrink: 0;
  min-height: 0;
  position: relative;
}
.tile .data {
  position: absolute;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  pointer-events: none;
  opacity: 0;
  z-index: 20;
  -webkit-transform: translate3d(0,100%,0);
          transform: translate3d(0,100%,0);
}
.tile .data > div {
  width: 100%;
}
.tile .data, .tile .main {
  -webkit-transition: opacity .15s, -webkit-transform .3s, -webkit-filter .3s;
  transition: opacity .15s, -webkit-transform .3s, -webkit-filter .3s;
  transition: transform .3s, opacity .15s, filter .3s;
  transition: transform .3s, opacity .15s, filter .3s, -webkit-transform .3s, -webkit-filter .3s;
}
.tile.show-data .data {
  pointer-events: all;
  opacity: 1;
  -webkit-transform: translate3d(0,0,0);
          transform: translate3d(0,0,0);
}
.tile.show-data .main {
  pointer-events: none;
  -webkit-filter: blur(30px);
          filter: blur(30px);
  -webkit-transform: translate3d(0,0,0);
          transform: translate3d(0,0,0);
}
.tile .data-toggle {
  position: absolute;
  top: 3vh;
  right: 3vh;
  padding: .5em;
  height: 2em;
  width: 2em;
  cursor: pointer;
  -webkit-transition: opacity .2s, -webkit-transform .1s;
  transition: opacity .2s, -webkit-transform .1s;
  transition: transform .1s, opacity .2s;
  transition: transform .1s, opacity .2s, -webkit-transform .1s;

  display: flex;
  justify-content: center;
  align-items: center;
}
.tile .data-toggle:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}
.tile .data-toggle:active {
  -webkit-transform: scale(.8);
          transform: scale(.8);
}
.tile .data-toggle:hover svg,
.tile .data-toggle.active svg {
  fill: white;
}
.tile .data-toggle svg {
  fill: hsla(220, 39%, 65%, .8);
  height: 100%;
  width: 100%;
}

